.navbar {
  display: block;
}
.navbar a {
  font-size: 1.25rem;
  font-weight: 600;
  text-align: center;
  background-image: linear-gradient(to right, #044577, #044577 50%, #044577 100%);
  background-size: 200% 100%;
  background-position: -100%;
  display: inline-block;
  padding: 5px 0;
  position: relative;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all 0.3s ease-in-out;
}
.navbar a:before {
  content: "";
  background: #044577;
  display: block;
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 0;
  height: 3px;
  transition: all 0.6s ease-in-out;
}
.navbar li {
  padding-left: 0.75rem;
}
@media (min-width: 768px) {
  .navbar a {
    width: 11rem;
