  transition: color 0.5s;
  transition: border-bottom 0.5s;
  border: 1px solid transparent;
}
.navbar-light .navbar-nav .nav-link:hover {
  background-position: 0;
}
.navbar-light .navbar-nav .nav-link:hover::before {
  width: 100%;
}

