/* CARD EFFECT */
@media (min-width: 576px) {
  .main-service {
    transition: all 0.5s;
  }
  .main-service .element {
    transition: all 0.5s;
    position: relative;
  }
  .main-service .element:hover {
    transition: all 0.5s;
  }
  .main-service .element:hover .title-service {
    display: none;
    transition: all 0.5s;
  }
  .main-service .element:hover .img-gradient {
    opacity: 0.25;
    transition: all 0.5s;
  }
  .main-service .element:hover .main-services {
    padding: 4rem;
    display: block;
    position: absolute;
    z-index: 102;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    width: 100%;
    color: #EEEEEE;
    gap: 5rem;
    color: #b1dbfc;
    font-weight: 600;
    font-size: 1.25rem;
    transition: all 0.5s;
  }
  .main-service .element:hover .main-services .materials {
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    transition: all 0.5s;
  }
}
@media (min-width: 576px) and (min-width: 768px) {
  .main-service .element:hover .main-services {
    padding: 8rem;
    grid-template-columns: repeat(2, 1fr);
    font-weight: 600;
