@use "base/mixins" as m;

.AFFA-info {
  .display-large {
    display: none;
    @include m.telefonoSM {
      display: inline;
    }
  }
  .name-extra {
    display: block;
    @include m.telefonoSM {
      display: inline;
    }
  }
  .affa-text,
  .affa-image {
    margin-right: 1rem;
    margin-left: 1rem;
  }
  .affa-text {
    margin-bottom: 2rem;
  }
  // margin-top: 2rem;
  text-align: center;
  img {
    height: 15rem;
    object-fit: cover;
  }
  @include m.telefonoSM {
    .affa-image {
      img {
        height: 26rem;
      }
    }
  }
  @include m.telefono {
    .affa-image {
      img {
        height: 40rem;
        object-fit: cover;
      }
    }
  }
  @include m.tablet {
    // margin-top: 4rem;}
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
    .affa-image {
      img {
        height: 30rem;
        object-fit: cover;
      }
    }
    h3 {
      margin: 0;
      margin-top: 1rem;
    }
    p {
      text-align: justify;
      margin-bottom: 1rem;
    }
    .affa-text {
      margin: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
    }
  }
  @include m.desktop {
    .affa-image {
      img {
        height: 35rem;
      }
    }
  }
}

.text-short {
  display: block;
}

.text-long {
  display: none;
}

@include m.desktop {
  .text-long {
    display: block;
  }
  .text-short {
    display: none;
  }
}

ul {
  padding-left: 0;
  li {
    list-style-type: none;
    display: block;
  }
}

.section-us-grid {
  display: flex;
  flex-direction: column;
  .us-img {
    order: -1;
    margin: auto;
    @include m.desktopXL {
      margin: 0;
    }
  }
  @include m.telefonoSM {
    .us-img {
      width: 80%;
    }
  }
  @include m.telefono {
    .us-img {
      width: 50%;
    }
  }
  @include m.desktopXL {
    .us-img {
      width: 100%;
      order: 1;
      margin-top: 3rem;
      display: flex;
      align-items: flex-start;
      padding-right: 2rem;
    }
    display: grid;
    grid-template-columns: 5fr 4fr;
    gap: 2rem;
    width: 80%;
    margin: auto;
    .section-us-grid {
      h3 {
        text-align: left;
      }
      .text-center {
        text-align: left;
      }
    }
  }
}
