@use "variables" as v;
@use "base/mixins" as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

element.style {
  padding-left: unset;
}

*,
*:before,
*:after {
  font-family: "Montserrat", sans-serif;
  box-sizing: inherit;
}

body {
  background-color: rgba($color: v.$azul, $alpha: 1);
  line-height: 1.5;
  height: 100vh;
  margin: 0;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
  margin: 0 1rem 1rem 1rem;
  // @include m.desktop {
  //   margin: 3rem 5rem;
  // }
}

figure {
  margin: 0;
}

.section-container {
  margin: 0rem 1rem 0rem 1rem;
  @include m.telefonoSM {
    margin-left: 2rem;
    margin-right: 2rem;
  }
  @include m.telefono {
    margin-left: 2.5rem;
    margin-right: 2.5rem;
  }
  @include m.desktop {
    max-width: 95rem;
    margin-left: auto;
    margin-right: auto;
  }
  @include m.desktopXL {
    max-width: 110rem;
  }
}

h2 {
  color: v.$blanco;
  margin: 2rem;
  text-align: center;
  font-size: 2rem;
  @include m.telefonoSM {
    margin: 2rem;
    font-size: 2.25rem;
  }
  @include m.desktop {
    font-size: 2.5rem;
  }
}

h3 {
  color: v.$blanco;
  margin: 1rem;
  text-align: center;
  font-size: 1.4rem;
  @include m.telefonoSM {
    margin: 2rem;
    font-size: 1.6rem;
  }
  @include m.desktop {
    font-size: 2.1rem;
  }
}

.modal-header {
  h3 {
    margin: 0;
  }
}

label,
.modal-title,
.btn {
  font-size: 1.2rem;
  @include m.telefonoSM {
    font-size: 1.4rem;
  }
  @include m.tablet {
    font-size: 1.4rem;
  }
  @include m.desktop {
    font-size: 1.4rem;
  }
}

p,
li,
.modal-body,
.invalid-feedback,
.thanks-home {
  font-size: 1.4rem;
  @include m.tablet {
    font-size: 1.45rem;
  }
  @include m.desktopXL {
    font-size: 1.65rem;
  }
}

.invalid-feedback {
  color: lighten(v.$naranja,10%);
  font-weight: 500;
}

.form-control {
  color: v.$oscuro;
  font-size: 1.2rem;
  @include m.tablet {
    font-size: 1.3rem;
  }
  @include m.desktop {
    font-size: 1.3rem;
  }
}

label {
  color: v.$beige;  
}

.modal-body {
  p {
    font-size: 1.15rem;
    @include m.telefono {
      font-size: 1.3rem;
    }
  }
}

.container {
  max-width: 120rem;
  padding: 0 2rem;
  @include m.telefonoSM {
    padding: inherit 4rem;
  }
}

a {
  text-decoration: none;
  color: v.$primario;
}

img {
  width: 100%;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

footer {
  margin-top: auto;
  text-align: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.main-section {
  margin-top: 2rem;
  @include m.telefono {
    margin-top: 3rem;
  }
}